<template>
    <div>
      <el-form>
        <el-form-item>
          <el-input
            type="textarea"
            v-model="content"
            placeholder="拷贝json数据..."
            rows="5"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="transfer">确认生成</el-button>
        </el-form-item>
      </el-form>
      <div style="margin-bottom: 10px;color: #666; font-size: 16px; text-align: left">表单内容如下</div>
      <div style = "border: 1px solid #eee;padding: 10px">
        <fm-generate-form
          :data='jsonData'
          ref='generateForm'
        >
        </fm-generate-form>
        <el-button type='primary' @click='handleSubmit'>Submit</el-button>
      </div>
    </div>
</template>

<script>
export default {
  name: 'FormGenerator',
  data () {
    return {
      content: JSON.stringify(this.jsonData),
      jsonData: {
        'list': [{
          'type': 'input',
          'icon': 'icon-input',
          'options': {
            'width': '100%',
            'defaultValue': '',
            'required': false,
            'dataType': 'string',
            'pattern': '',
            'placeholder': '',
            'customClass': '',
            'disabled': false,
            'labelWidth': 100,
            'isLabelWidth': false,
            'hidden': false,
            'dataBind': true,
            'showPassword': false,
            'remoteFunc': 'func_1575897887618',
            'remoteOption': 'option_1575897887618'
          },
          'name': '单行文本',
          'key': '1575897887618',
          'model': 'input_1575897887618',
          'rules': [{
            'type': 'string',
            'message': '单行文本格式不正确'
          }]
        }],
        'config': {
          'labelWidth': 100,
          'labelPosition': 'right',
          'size': 'small',
          'customClass': ''
        }
      }
    }
  },
  methods: {
    transfer () {
      if (!this.content) {
        return
      }
      console.log(this.content)
      this.jsonData = JSON.parse(this.content)
    },
    handleSubmit () {
      this.$refs.generateForm.getData().then(data => {
        alert(JSON.stringify(data))
      }).catch(e => {
      })
    }
  }
}
</script>

